.movies .movie {
	display: flex;
	width: 45%;
	margin-bottom: 70px;
	padding: 20px;
	justify-content: space-between;
	align-items: flex-start;
	font-weight: 300;
	color: #adaeb9;
	background-color: #fff;
	border-radius: 5px;
	box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25), 0 8px 16px -8px rgba(0, 0, 0, 0.3),
		0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.movie img {
	position: relative;
	top: -50px;
	width: 100%;
	max-width: 180px;
	margin-right: 30px;
	box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25), 0 18px 36px -18px rgba(0, 0, 0, 0.3),
		0 -12px 36px -8px rgba(0, 0, 0, 0.025);
}

.movie .movie_title,
.movie .movie_year {
	margin: 0;
	font-weight: 300;
}

.movie .movie_title {
	margin-bottom: 5px;
	font-size: 24px;
	color: #2c2c2c;
}

.movie .movie_genres {
	list-style: none;
	display: flex;
	margin: 5px 0;
	padding: 0;
}

.movie_genres li {
	margin-right: 10px;
	font-size: 14px;
}
